* {
  box-sizing: border-box;
}

body {
  justify-content: center;
  align-items: center;
  display: flex;
}

.clock {
  border: 4px dotted;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.clock__centr {
  background-color: #ff0;
  border: 1px;
  border-radius: 100%;
  width: 60px;
  height: 60px;
  position: relative;
}

.clock__hand--hours {
  transform-origin: 0;
  background-color: #00f;
  width: 210px;
  height: 30px;
  margin-left: 190px;
  animation: 60s linear infinite rotate-hands;
  position: absolute;
}

.clock__hand--minute {
  transform-origin: bottom;
  background-color: green;
  width: 15px;
  height: 280px;
  margin-bottom: 275px;
  animation: 5s linear infinite rotate-hands;
  position: absolute;
}

@keyframes rotate-hands {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(600deg);
  }
}

